video {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: relative;
  z-index: -1;
}

.hero-container {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
  object-fit: contain;
}
#overlay-text{
  position: absolute;
  top:40%;
  left: 15%;
  z-index: 1;

}

#overlay-text > h1 {
  color: #fff;
  font-size: 45px;
 }

.hero-container > p {
  margin-top: 8px;
  color: #fff;
  font-size: 32px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
}

#hero-but {
  margin-top: 32px;
  background-color:rgb(0,0.1,0.4);
  border-radius: 8px;
  cursor: pointer;
  padding: 15px;
  color: #fff;
  font-size: 15px;
  font-weight: 20px;
}

.hero-btns .btn {
  margin: 6px;
}

.fa-play-circle {
  margin-left: 4px;
}

@media screen and (max-width: 960px) {
  .hero-container > h1 {
    font-size: 70px;
    margin-top: -150px;
  }
}

@media screen and (max-width: 768px) {
  .hero-container > h1 {
    font-size: 50px;
    margin-top: -100px;
  }

  .hero-container > p {
    font-size: 30px;
  }

  .btn-mobile {
    display: block;
    text-decoration: none;
  }

  .btn {
    width: 100%;
  }
}
